function Socket(vm){
	var html = `<div id="reconnection">
	    <div class="reconnection">
	    	<div class="spinner">
				  <div class="cube1"></div>
				  <div class="cube2"></div>
				</div>
				<p>...</p>
	    </div>
		<div class="mask-canvas-backdrop"></div>
    <div>`;
	var els = document.getElementsByTagName('body')[0];
	var socket = vm.socket;
	//
	var Whether = true;
	//
	var count = 0;
	//
	var timer = null;
	//0
	socket.on('nologin', function(data){
		console.log('');
		if(data.login === 0){
			Whether = false;
		}
	});
	socket.on('disconnect', function(data){
		console.log('');
		clearInterval(timer);
		//
		if(Whether && count<10){
			var reconnection = document.getElementById('reconnection');
			var initiative = vm.$store.getters.getinitiative; //
			if(!reconnection && initiative != 1){
				els.appendChild(parseDom(html)[0]);//
			}
			//10
			var reconnec = function(){
				count++;
				console.log('',count);
				socket.connect(global.IO);
			  	if(count>=10){
					var reconnection = document.getElementById('reconnection');
					if(reconnection){
						reconnection.parentNode.removeChild(reconnection);
					}
					mui.toast('')
			  		clearInterval(timer);
			  	}
			};
			reconnec();
			timer = setInterval(reconnec, 10000);
		}else{
			console.log('')
		}
	});
	
	socket.on('reconnecp', function(data){
		var reconnection = document.getElementById('reconnection');
		if(reconnection){
			reconnection.parentNode.removeChild(reconnection);
		}
		Whether = true;
  		console.log('');
  		count = 0;
  		clearInterval(timer); //
	});
}


function parseDom(arg) {

 var objE = document.createElement("div");

 objE.innerHTML = arg;

 return objE.childNodes;

};
export default Socket;


